/*---   GENERAL    ---- */
a,
a:link,
a:visited,
a:hover,
a:active {
  color: black;
  text-decoration: none;
  background-color: transparent;
}

li {
  list-style-type: none;
}

.menu {
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.topHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px;
}

.topHeader .logo {
  height: 60px;
  width: 250px;
}

.dropdownMenu {
  display: none;
}

.hamburger {
  height: 45px;
}

.navigationHeader {
  display: flex;
  justify-content: center;
}

.navigationHeader nav ul li {
  width: 100%;
}

.navigationHeader nav ul li a {
  font-size: 1.6em;
}

input[type='checkbox'] ~ .nav {
  display: none;
}

input[type='checkbox']:checked ~ nav {
  display: block;
}

@media screen and (min-width: 940px) {
  /* ---   Header   --- */
  .hamburger {
    display: none;
  }
  /* Mettre le header en ligne*/
  .menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .navigationHeader {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  input[type='checkbox'] ~ .nav {
    display: block;
  }
  input[type='checkbox'] ~ .nav ul {
    display: flex;
    flex-direction: row;
  }
  .nav li a {
    margin: 30px;
  }
}

/* ------- MIXIN RACCOURCIS ------- */
/* ------- STYLE POUR ECRAN TAILLE SMALL: min-width:0px ------- */
@media (min-width: 0px) {
  header {
    min-height: 150px;
    max-height: 350px;
    height: 50vw;
    background-color: red;
  }
  header .menu {
    height: 40px;
    background-color: green;
  }
  .main {
    height: auto;
    height: 250px;
  }
  .main .contain {
    background-color: yellow;
    height: 100%;
  }
  .main .bloc:nth-child(1) {
    background-color: blue;
    height: 70vw;
    min-height: 150px;
    max-height: 400px;
  }
}

/* ------- STYLE POUR ECRAN TAILLE MEDIUM ------- */
@media (min-width: 800px) {
  header {
    height: 350px;
    background-color: red;
  }
  .main {
    background-color: green;
    height: auto;
  }
  .main .contain {
    max-width: 800px;
    height: 100%;
    margin: 0 auto;
  }
  .main .bloc:nth-child(1) {
    background-color: blue;
    height: 400px;
  }
  .main .bloc:nth-child(2) {
    background-color: brown;
    height: 300px;
  }
  .main .bloc:nth-child(3) {
    background-color: pink;
    height: 50px;
  }
  .main .bloc:nth-child(4) {
    background-color: yellow;
    height: 100px;
  }
  .main .bloc:nth-child(5) {
    background-color: orange;
    height: 50px;
  }
  .articles {
    height: auto;
    background-color: green;
  }
  .articles .contain {
    background-color: red;
    width: 65%;
    height: 100%;
    margin: 0 auto;
  }
  .articles .bloc {
    margin-bottom: 10px;
  }
  .articles .bloc:nth-child(1) {
    background-color: blue;
    height: 300px;
  }
  .articles .bloc:nth-child(2) {
    background-color: pink;
    height: 300px;
  }
  .articles .bloc:nth-child(3) {
    background-color: yellow;
    height: 300px;
  }
  .articles .bloc:nth-child(4) {
    background-color: brown;
    height: 300px;
  }
  footer {
    height: 200px;
    background-color: black;
  }
}
